* {
  margin: 0;
  padding: 0;
}

html {
  min-height: 100%;
  position: relative;
}

body {
  font-family: 'DISTANT GALAXY', Verdana, sans-serif;
  background-color: black;
  color: white;
  height: 100%;
  margin: 0;
}

div {
  letter-spacing: 0.1px;
}

canvas {
  display: block;
  vertical-align: bottom;
}

.particles-js {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
}

@font-face {
  font-family: 'DISTANT GALAXY';
  font-style: normal;
  font-weight: normal;
  src: local('DISTANT GALAXY'), url('../media/distant_galaxy.ttf') format('truetype');
}

#home {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#home a {
  border: 2px solid goldenrod;
  color: white;
  text-align: center;
  padding: 1.8em;
  text-decoration: none;
  -ms-flex: 1 0 46%;
      flex: 1 0 46%;
  font-size: 1.5em;
  margin: 0.5em;
  border-radius: 20px;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

#home a:hover {
  text-decoration: underline;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

#main-content {
  letter-spacing: 5px;
  padding: 0 2em;
  max-width: 900px;
  margin: 0 auto;
}

#header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  max-width: 1100px;
  margin: 0 auto;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 5px;
}

.hidden {
  visibility: hidden;
}

.hidden > a {
  cursor: default;
}

.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.obvious {
  padding: 2em;
  border: 5px solid goldenrod;
  font-size: 3em;
  text-decoration: none;
  -webkit-transition: font-size .2s ease-in-out;
  -o-transition: font-size .2s ease-in-out;
  transition: font-size .2s ease-in-out;
  color: white;
}

.obvious:hover {
  font-size: 3.5em;
  text-decoration: underline;
}

@media screen and (max-width: 480px) {
  #home a {
    font-size: 1em;
  }

  #header {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  h1.title {
    margin: 0;
  }
}



